html{
  box-sizing: border-box;
}

*, *::before, *::after{
  box-sizing: inherit;
}

.main{
  margin: 0 auto;
}

.header{
  max-width: 1280px;
  min-width: 345px;
  min-height: 24px;
  background-color: black;
  margin: 0 auto;
}

.top_bar{
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
}

.phone{
  display: inline;
  margin-right: 5px;
  opacity: 0.8;
}

.nomer{
  display: inline-block;
  font-family: Spartan;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 20px;
  margin-right: 10px;

  color: #FFFFFF;

  opacity: 0.8;
}

.vector{
  display: inline;
  margin-right: 5px;
  opacity: 0.8;
}

.location{
  display: inline;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 20px;
  letter-spacing: 0.03em;
  margin-right: 10px;

  color: #FFFFFF;

  opacity: 0.8;
}

.clock{
  display: inline;
  margin-right: 5px;
  opacity: 0.8;
}

.time{
  display: inline;
  font-family: Spartan;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 20px;

  color: #FFFFFF;

  opacity: 0.8;
}

.group_1{
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}

.group_2{
  display: inline-block;
}

.logo{
  margin-right: 10px;
}

.nav{
  max-width: 1110px;
  min-height: 66px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
  padding-bottom: 25px;
}
.nav_logo{
  font-family: Playfair Display SC;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  color: #121212;
  text-decoration: none;
  margin-right: 90px;
  vertical-align: middle;
}

.nav_menu{
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #121212;
  text-decoration: none;
  margin-right: 30px;
}

@media (max-width: 850px){
  .nav_menu{
      max-height: 0;
      background: white;
      float: none;
      position: absolute;
      overflow: hidden;
      top: 63px;
      right: 0;
      left: 0;
      margin: 0;
      padding: 0;
      z-index: 3;
  }
}

.content__banners{
  max-width: 1110px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  margin: 0 auto;
}

@media (max-width: 1110px){
  .content__banners{
    grid-template-columns: repeat(1, 1fr);
  }
}

.content__banner__grid{
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 26px;
}

.content__banner__sm__grid{
  max-width: 540px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
}

.banner{
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

.ban_1{
  max-height: 490px;
}

.ban_2{
  max-height: 200px;
}

.ban_3{
  max-height: 200px;
}

.ban_4{
  max-height: 260px;
}

/* advantage start */

.advantage{
  max-width: 1281px;
  height: 103px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 85px;
  margin: 0 auto;
}

@media (max-width: 1110px){
  .advantage{
    grid-template-columns: repeat(2, 1fr);
    height: 206px;
  }
}

@media (max-width: 800px){
  .advantage{
    grid-template-columns: repeat(1, 1fr);
    height: 309px;
  }
}

@media (max-width: 490px){
  .advantage{
    grid-template-columns: repeat(1, 1fr);
    height: 500px;
  }
}

.advantage__group{
  margin: 32px 0;
  display: flex;
  flex-wrap: wrap;
}

.advantage__group__pic{
  margin-right: 8px;
}

.advantage__group__text__lg{
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  display: flex;
  align-items: center;
  letter-spacing: 0.06em;
}

.advantage__group__text__sm{
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  color: #121212;
  opacity: 0.9;
}

/* advantage end */

/* women start */

.women{
  max-width: 1110px;
  margin: 100px auto;
}

.women__intro{
  height: 25px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 50px;
}

.women__intro__tx{
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 25px;
  display: flex;
  align-items: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #121212;
}

.women__intro__filters{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 850px){
  .women__intro__filters{
      max-height: 0;
      background: white;
      float: none;
      position: absolute;
      overflow: hidden;
      top: 63px;
      right: 0;
      left: 0;
      margin: 0;
      padding: 0;
      z-index: 3;
  }
}

.women__intro__filter{
  margin-right: 32px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 13px;
  text-align: right;
  letter-spacing: 0.06em;
  color: #121212;
}

.women__intro__filter:last-child{
  margin-right: 0px;
}

.women__content{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  margin-bottom: 50px;
}

@media (max-width: 1160px){
  .women__content{
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 840px){
  .women__content{
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 560px){
  .women__content{
    grid-template-columns: repeat(1, 1fr);
  }
}

.women__content__item{
  margin: 0 auto;
}

.women__content__item__info{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.women__content__item__name{
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #121212;
  opacity: 0.6;
}

.women__content__item__price{
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #121212;
}

.women__seeall__btn{
  width: 100%;
  height: 50px;
  background: #F8F8F8;
  border: 2px solid #F8F8F8;
  box-sizing: border-box;
}

.women__seeall__btn__tx{
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #121212;
  opacity: 0.8;
}

/* women end */

/* banner5and6 start */

.banner5and6{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  max-width: 1110px;
  margin: 0 auto 100px auto;
}

@media (max-width: 1160px){
  .banner5and6{
    grid-template-columns: repeat(1, 1fr);
  }
}

.banner5and6__b{
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

/* banner5and6 end */

/* sabscribe__banner start */

.sabscribe__banner{
  max-width: 1280px;
  height: 620px;
  object-fit: cover;
  margin: 0 auto;
  padding-top: 60px;
  background: url(./pictures/jpg/email_ph.jpg) no-repeat center;
}

@media (max-width: 840px){
  .sabscribe__banner{
    background-image: none;
  }
}

.sabscribe__banner__info{
  max-width: 530px;
  height: 460px;
  margin: 0 auto;
}

.sabscribe__banner__info__box{
  max-width: 340px;
  margin: 0 auto;
  margin-top: 80px;
  text-align: center;
}

.sabscribe__banner__info__smtx{
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #121212;
  opacity: 0.4;
  margin-bottom: 16px;
}

.sabscribe__banner__info__lgtx{
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 36px;
  color: #121212;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  margin-bottom: 32px;
}

.red{
  color: rgb(255, 0, 128);
}

.sabscribe__banner__info__input{
  width: 340px;
  height: 64px;
  background: #F8F8F8;
  border: 2px solid #F8F8F8;
  box-sizing: border-box;
  text-align: center;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 24px;
}

.sabscribe__banner__info__btn{
  width: 340px;
  height: 64px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  background-color: black;
  color: #FFFFFF;
  opacity: 0.9;
}

/* sabscribe__banner end */

/* blog start */

.blog{
  max-width: 1110px;
  margin: 0 auto;
}

.blog__intro{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 50px;
}

.blog__intro__name{
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 25px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.blog__intro__seeall{
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 13px;
  text-align: right;
  letter-spacing: 0.06em;
  color: #121212;
  opacity: 0.6;
  margin: auto 0;
}

.blog__contents{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}

.blog__content{
  margin: 0 auto;
}

@media (max-width: 1160px){
  .blog__contents{
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 750px){
  .blog__contents{
    grid-template-columns: repeat(1, 1fr);
  }
}

.blog__content__pic{
  max-width: 100%;
  height: auto;
}

.blog__content__tx{
  width: 300px;
  height: 175px;
  margin: 0 auto;
  background-color: white;
  transform: translateY(-50px);
  padding-left: 16px;
  padding-top: 24px;
}

.blog__content__tx__lg{
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #121212;
  margin-bottom: 12px;
}

.blog__content__tx__sm{
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #121212;
  opacity: 0.8;
}

/* blog end */

/* futer start */

.futer{
  max-width: 1280px;
  margin: 0 auto;
}

.futer__start{
  height: 56px;
  background-color: #121212;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 85px;
  padding-right: 85px;
}

.futer__start__info{
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 19px 0px 19px 0px;
}

@media (max-width: 980px){
  .futer__start{
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 850px){
  .futer__start__input{
    max-height: 0;
    background: white;
    float: none;
    position: absolute;
    overflow: hidden;
    top: 63px;
    right: 0;
    left: 0;
    margin: 0;
    padding: 0;
    z-index: 3;
  }
}

.futer__start__input__email{
  width: 400px;
  height: 32px;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  color: #121212;
  opacity: 0.4;
  background-color: #FFFFFF;
  margin: 12px 12px 12px 0px;
}

.futer__start__input__btn{
  width: 108px;
  height: 32px;
  background-color: #121212;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  opacity: 0.9;
}

.futer__start__share{
  margin: 19px 0px 19px 0px;
}

.futer__start__share__logo{
  margin-right: 12px;
}

.futer__start__share__logo:last-child{
  margin-right: 0px;
}

.futer__links{
  height: 237px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  padding-left: 85px;
  padding-right: 85px;
  padding-top: 40px;
}

@media (max-width: 915px){
  .futer__links{
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 140px;
  }
}

@media (max-width: 535px){
  .futer__links{
    grid-template-columns: repeat(1, 1fr);
    margin-bottom: 520px;
  }
}

.futer__links__grid{
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  height: 157px;
}

.futer__links__info{
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  display: flex;
  align-items: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #121212;
  margin-bottom: 8px;
}

.futer__link{
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 34px;
  color: #121212;
  opacity: 0.8;
  text-decoration: none;
}

.futer__links__info__contact{
  display: flex;
  flex-wrap: wrap;
  margin: auto 0;
}

.futer__links__info__contact__logo{
  margin-right: 8px;
}

.futer__info{
  height: 40px;
  padding: 0 85px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background: #F8F8F8;
}

.futer__info__cp{
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 22px;
  color: #121212;
  opacity: 0.8;
  margin: auto 0;
}

.futer__info__pic{
  margin: auto 0;
}

@media (max-width: 950px){
  .futer__info__pic{
    max-height: 0;
    background: white;
    float: none;
    position: absolute;
    overflow: hidden;
    top: 63px;
    right: 0;
    left: 0;
    margin: 0;
    padding: 0;
    z-index: 3;
  }
}

.futer__info__mail{
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 22px;
  text-align: right;
  text-decoration-line: underline;
  color: #121212;
  opacity: 0.8;
  margin: auto 0 auto 10px;
}